import React from 'react';
import classNames from 'classnames';
import { iconButtonStyles } from '../../styles/common-styles';

export interface IconButtonProps extends React.ButtonHTMLAttributes<HTMLButtonElement> {
  readonly variant?: 'filled' | 'bare';
  readonly label: string;
}

export const IconButton: React.FC<IconButtonProps> = React.forwardRef<
  HTMLButtonElement,
  IconButtonProps
>(({ label, variant = 'bare', className, children, ...props }, ref) => {
  const visuallyHidden = <span className="visually-hidden">{label}</span>;
  return variant === 'filled' ? (
    <button type="button" className={classNames(iconButtonStyles, className)} ref={ref} {...props}>
      {children}
      {visuallyHidden}
    </button>
  ) : (
    <button type="button" className={className} ref={ref} {...props}>
      {children}
      {visuallyHidden}
    </button>
  );
});

IconButton.displayName = 'IconButton';
